<template>
    <ElTable
        :data="dailyList"
        height="300"
        stripe
        size="mini"
        style="width: 100%"
        :default-sort="{prop: 'date', order: 'descending'}"
    >
        <ElTableColumn prop="date" label="日期" width="180" sortable />
        <ElTableColumn
            prop="scene"
            label="场景"
            width="150"
            :filters="filterOptions.scene"
            :filter-method="userInfoFilterHandler"
        />
        <ElTableColumn prop="sync" label="采集" width="120" />
        <ElTableColumn prop="submitSelect" label="选品" width="120" />
        <ElTableColumn prop="submitInfo" label="价格申报" width="120" />
        <ElTableColumn prop="submitDetail" label="完善详情" width="120" />
    </ElTable>
</template>

<script>
import { axiosGet } from '../libs/request';
import { resolveData } from '../util/business';
import { FILTER_OPTIONS } from '../constant/filterOptions';

export default {
    name: 'Daily',

    data() {
        return {
            dailyList: [],
            filterOptions: FILTER_OPTIONS,
        };
    },
    created() {
        this.getData();
    },

    methods: {
        async getData() {
            const { data } = await axiosGet('/count/user_info');
            this.dailyList = resolveData(data);
        },
        userInfoFilterHandler(value, row, column) {
            return row[column.property] === value;
        },
    },
};
</script>
